export const roofLayers: any = [
  {
    name: '   ',
    layerId: 1,
    composition: {
      material1: '',
      material2: '',
      scopeOfWork(
        squareMeters: number,
        lowerPoint: number,
        upperPoint: number
      ) {
        let averageThickness = (((upperPoint - lowerPoint) / 3) * 2) / 100;
        let keramzitArea = squareMeters * averageThickness;
        let tape = squareMeters;
        this.material1 = ' - ' + keramzitArea.toString() + ' 3';
        this.material2 = '  - ' + tape.toString() + ' 2';
      },
    },
  },
  {
    name: '   //',
    layerId: 2,
    composition: {
      material1: '',
      material2: '',
      material3: '',
      scopeOfWork(
        squareMeters: number,
        lowerPoint: number,
        upperPoint: number
      ) {
        let averageThickness = (((upperPoint - lowerPoint) / 3) * 2) / 100;
        let slopeArea = squareMeters * averageThickness;
        let reinforcingMesh = squareMeters / (1.8 * 2.3);
        let retainers = squareMeters * 2.5;
        this.material1 = ' - ' + slopeArea.toString() + ' 3';
        this.material2 =
          '  - ' + reinforcingMesh.toString() + ' 2';
        this.material3 = ' - ' + retainers.toString() + ' ';
      },
    },
  },
  {
    name: '',
    layerId: 3,
    composition: {
      material1: '',
      material2: '',
      material3: '',
      scopeOfWork(squareMeters: number) {
        let reinforcingMesh = squareMeters / (1.8 * 2.3);
        let retainers = squareMeters * 2.5;
        let screed = squareMeters * 0.06;
        this.material1 =
          '  - ' + reinforcingMesh.toString() + ' 2';
        this.material2 = ' - ' + retainers.toString() + ' ';
        this.material3 = ' - ' + screed.toString() + ' 2';
      },
    },
  },
  { name: '', layerId: 4 },
  { name: ' ', layerId: 5 },
  {
    name: '',
    layerId: 6,
    composition: {
      material1: '',
      material2: '',
      scopeOfWork(squareMeters: number) {
        let primer = squareMeters * 0.25;
        let waterproofing = squareMeters / 8.75;
        this.material1 = ' - ' + primer.toString() + ' ';
        this.material2 =
          ' 1  - ' + waterproofing.toString() + ' .';
      },
    },
  },
  { name: '', layerId: 7 },
  {
    name: ' ',
    layerId: 8,
    composition: {
      material1: '',
      material2: '',
      scopeOfWork(squareMeters: number) {
        let rubble = squareMeters * 0.05;
        let Geotextile = squareMeters;
        this.material1 = ' - ' + rubble.toString() + ' 3';
        this.material2 = ' - ' + Geotextile.toString() + ' 2';
      },
    },
  },
];

